import React, { Fragment, useEffect, useState } from "react"
import { Row, Button, Col, Card, CardBody, Breadcrumb, BreadcrumbItem } from "reactstrap"
import { Colxx, Separator } from "../../../../components/common/CustomBootstrap"
import { ContextMenuTrigger } from "react-contextmenu"
import mUser from '../../../../Controler/c_User'
import mListStokMasuk from '../../../../Controler/c_StokMasuk'
import Skeleton from 'react-loading-skeleton'
import ModalProduk from './Modal.StokMasuk'
import Notifikasi from '../../../../helpers/Notifikasi'
import Confirm from '../../../../helpers/Confirm'
import IntlMessages from "../../../../helpers/IntlMessages"
import DataTablePagination from "../../../../components/DatatablePagination"
import { isArray } from "util";
import ReactTable from "react-table"
import { MuiPickersUtilsProvider, DatePicker } from '@material-ui/pickers';
import DateFnsUtils from '@date-io/date-fns';
import moment from 'moment-timezone'

const Admin = (props) => {
    const authLogin = JSON.parse(localStorage.getItem('keminaAdmin'))
    const [isReload, setIsReload] = useState(true)
    const [listStokMasuk, setStokMasuk] = useState([])
    const [user, setUser] = useState({})
    const [detailProduk, setDetailProduk] = useState({})
    const [isConfirm, setIsConfirm] = useState(false)
    const [textConfirm, setTextConfirm] = useState("")
    const [isModal, setIsModal] = useState(false)
    const [tgldari, setTgldari] = useState(moment())
    const [tglSampai, setTglSampai] = useState(moment())


    useEffect(() => {
        mListStokMasuk.listStokMasuk(authLogin.clientID, authLogin.token, authLogin.klinik.kodeklinik, moment(tgldari).format("YYYY-MM-DD"), moment(tglSampai).format("YYYY-MM-DD")).then(resp => {
            // console.log(resp, 'resp')
            setStokMasuk(resp);
        }).catch(err => {
            console.error(err);
        }).finally(() => setIsReload(false));
        return () => { };
    }, [authLogin.clientID, authLogin.token, authLogin.username, authLogin.klinik.kodeklinik, isReload, tgldari, tglSampai]);
    useEffect(() => {
        mUser.dataUser(authLogin.clientID, authLogin.token, authLogin.username, authLogin.jenisUser).then(resp => {
            setUser(resp);
        }).catch(err => {
            console.error(err);
        })
        return () => {

        }
    }, [authLogin.clientID, authLogin.token, authLogin.username, authLogin.jenisUser])
    const handleAdd = () => {
        setDetailProduk({})
        setIsModal(true)
    }
    const handleEdit = (data) => {
        setDetailProduk(data.original)
        setIsModal(true)
    }
    const confirmHapus = (data) => {
        setDetailProduk(data.original)
        setTextConfirm(`Yakin ingin hapus Produk ${data.original.namaProduk}? Data tidak dapat dikembalikan setelah hapus`)
        setIsConfirm(true)
    }
    const handleHapus = () => {
        mListStokMasuk.hapusStokMasuk(authLogin.clientID, authLogin.token, authLogin.klinik.kodeklinik, detailProduk.notrans).then(resp => {
            if (resp) {
                Notifikasi("success", `Produk ${detailProduk.namaProduk} berhasil dihapus`, "")
                setIsReload(true)
                setIsConfirm(false)
            } else {
                Notifikasi("error", "Server tidak dapat menjalankan perintah Anda saat ini. Silakan coba sesaat lagi", "Gagal Simpan")
            }
        }).catch(err => {
            if (isArray(err) && err.length > 0) {
                Notifikasi("error", err[0].message, "Gagal Simpan")
            } else if (typeof err) {
                Notifikasi("error", err.message, "Gagal Simpan")
            } else {
                console.log(err);
            }
        })
    }
    const dataTableColumns = [
        {
            Header: "Tanggal Masuk",
            accessor: "tanggal",
            Cell: props => <p className="list-item-heading">{isReload ? <Skeleton /> : props.value}</p>
        },
        {
            Header: "No Transaksi",
            accessor: "notrans",
            Cell: props => <p className="list-item-heading">{isReload ? <Skeleton /> : props.value}</p>
        },
        {
            Header: "Kode Produk",
            accessor: "kodeProduk",
            Cell: props => <p className="list-item-heading">{isReload ? <Skeleton /> : props.value}</p>
        },
        {
            Header: "Nama Produk",
            accessor: "namaProduk",
            Cell: props => <p className="list-item-heading">{isReload ? <Skeleton /> : props.value}</p>
        },
        {
            Header: "Qty",
            accessor: "qty",
            Cell: props => <p className="list-item-heading">{isReload ? <Skeleton /> : props.value}</p>
        },
        {
            Header: "Satuan",
            accessor: "satuan",
            Cell: props => <p className="list-item-heading">{isReload ? <Skeleton /> : props.value}</p>
        },
        {
            Header: <p className="text-center">Aksi</p>,
            Cell: props => isReload ? <Skeleton /> : <p className="text-muted">
                <Button style={{ marginRight: 3 }} color="warning" onClick={handleEdit.bind(this, props)}>Edit</Button>
                <Button color="danger" onClick={confirmHapus.bind(this, props)}>Hapus</Button>
            </p>
        }
    ];
    if (user.jenisUser === "admin") {
        return (
            <Fragment>
                <Row>
                    <Colxx xxs="12">
                        <div className="mb-2">
                            <h1>
                                Inventory
                            </h1>
                            <Breadcrumb className="pt-0 breadcrumb-container d-none d-sm-block d-lg-inline-block">
                                <BreadcrumbItem>
                                    <IntlMessages id="menu.stok-masuk" />
                                </BreadcrumbItem>
                            </Breadcrumb>

                            <div className="text-zero top-right-button-container">
                                {user.superadmin && (
                                    <Button
                                        color="primary"
                                        size="lg"
                                        className="top-right-button"
                                        onClick={handleAdd}
                                    >
                                        Add  <IntlMessages id="menu.stok-masuk" />
                                    </Button>
                                )}
                            </div>
                        </div>

                        <Separator className="mb-5" />
                    </Colxx>
                </Row>
                <Row>
                <Col xs={6}>
                    <MuiPickersUtilsProvider utils={DateFnsUtils}>
                        <DatePicker
                            fullWidth={true}
                            label="Tanggal Dari"
                            format="dd-MM-yyyy"
                            value={tgldari}
                            onChange={(d) => setTgldari(d)}
                            ampm={false}
                        />
                    </MuiPickersUtilsProvider>
                </Col>
                <Col xs={6}>
                    <MuiPickersUtilsProvider utils={DateFnsUtils}>
                        <DatePicker
                            fullWidth={true}
                            label="Tanggal Sampai"
                            format="dd-MM-yyyy"
                            value={tglSampai}
                            onChange={(d) => setTglSampai(d)}
                            ampm={false}
                        />
                    </MuiPickersUtilsProvider>
                </Col>
                    <Colxx xxs="12" className="mb-4 jarakAtas">
                        <ContextMenuTrigger id="menu_id" >
                            <Card>
                                <CardBody>
                                    <ReactTable
                                        data={listStokMasuk}
                                        columns={dataTableColumns}
                                        defaultPageSize={5}
                                        filterable={false}
                                        showPageJump={true}
                                        PaginationComponent={DataTablePagination}
                                        showPageSizeOptions={true}
                                    />
                                </CardBody>
                            </Card>
                        </ContextMenuTrigger>
                    </Colxx>
                </Row>

                {isModal && (
                    <ModalProduk open={isModal} setOpen={setIsModal} clientID={authLogin.clientID} token={authLogin.token} setIsReload={setIsReload} detailProduk={detailProduk} klinik={authLogin.klinik} />
                )}
                {isConfirm && (
                    <Confirm open={isConfirm} setOpen={setIsConfirm} textConfirm={textConfirm} oke={handleHapus} />
                )}
            </Fragment>
        )
    } else {
        return (
            <p>You do not have access rights</p>
        )
    }
}
export default Admin;