import GQL from '../helpers/GQL'


const Produk = {
    cekStok: (clientID, token, kodeklinik) => {
        return new Promise((resolve, reject) => {
            const query = `
                query{
                    cekStok(kodeklinik:"${kodeklinik}"){
                    kodeProduk
                    namaProduk
                    kategori{
                        kodeKategoriProduk
                        kategoriProduk
                    }
                    jenis{
                        kodeJenisProduk
                        jenisProduk
                    }
                    harga
                    satuan
                    stokQty
                    }
                }
            `
            GQL(query, clientID, token).then(resp => {
                const listDatas = []
                resp.data.cekStok.map(el => {
                    // console.log(el)
                    if (el.kategori) {
                        var kategori = el.kategori.kategoriProduk
                        var kodekategori = el.kategori.kodeKategoriProduk
                    }

                    if (el.jenis) {
                        var jenis = el.jenis.jenisProduk
                        var kodejenis = el.jenis.kodeJenisProduk
                    }

                    return listDatas.push({
                        ...el,
                        kodekategori,
                        kategori,
                        kodejenis,
                        jenis,
                    })
                })
                if (resp.errors) return (reject(resp.errors));;
                if (resp.data && resp.data.cekStok) return (resolve(listDatas));
                return (resolve([]))
            }).catch(err => {
                return (reject(err));
            })
        })
    },
	listStokMasuk: (clientID, token, kodeklinik, tglDari, tglSampai) => {
		return new Promise((resolve, reject) => {
			const query = `
                query{
                    listStokMasuk(kodeklinik:"${kodeklinik}", tglDari:"${tglDari}", tglSampai:"${tglSampai}"){
                        tanggal,
                        notrans,
                        kodeProduk,
                        namaProduk,
                        qty,
                        satuan
                    }
                }
            `
			GQL(query, clientID, token).then(resp => {
                const listDatas = []
                resp.data.listStokMasuk.map(el => {
                    // console.log(el)
                    if (el.kategori) {
                        var kategori = el.kategori.kategoriProduk
                        var kodekategori = el.kategori.kodeKategoriProduk
                    } 

                    if (el.jenis) {
                        var jenis = el.jenis.jenisProduk
                        var kodejenis = el.jenis.kodeJenisProduk
                    } 

                   return listDatas.push({
                        ...el,
                        kodekategori,
                        kategori,
                        kodejenis,
                        jenis,
                    })
                })
				if (resp.errors) return (reject(resp.errors));;
				if (resp.data && resp.data.listStokMasuk) return (resolve(listDatas));
				return (resolve([]))
			}).catch(err => {
				return (reject(err));
			})
		})
    },
    simpanStokMasuk: (clientID, token,  kodeklinik, namaProduk, satuan, kode, qty) => {
        return new Promise((resolve, reject) => {
            const query =`
            mutation{
                simpanStokMasuk(
                    kodeklinik: "${kodeklinik}",input:{ 
                        kodeProduk: "${kode}", 
                        namaProduk: "${namaProduk}", 
                        qty: ${qty}, 
                        satuan:"${satuan}"
                    })
              }
        `
            GQL(query, clientID, token).then(resp => {
                if (resp.errors) return (reject(resp.errors));;
                if (resp.data && resp.data.simpanKlinik) return (resolve(resp.data.simpanKlinik));
                return (resolve([]))
            }).catch(err => {
                return (reject(err));
            })
        })
    },
	hapusStokMasuk: (clientID, token, kodeklinik, noTrans) => {
		return new Promise((resolve, reject) => {
			const query = `
			mutation {
                hapusStokMasuk(kodeklinik:"${kodeklinik}", notrans:"${noTrans}")
			  }
            `
			GQL(query, clientID, token).then(resp => {
				if (resp.errors) return (reject(resp.errors));
				if (resp.data && resp.data.hapusStokMasuk ) return (resolve(true));
				return (resolve([]))
			}).catch(err => {
				return (reject(err));
			})
		})
    },
    loadSelecBox: (clientID, token, kodeklinik) => {
		return new Promise((resolve, reject) => {
			const query = `
                query {
                    listKategoriProduk(kodeklinik:"${kodeklinik}"){
                        kodeKategoriProduk
                        kategoriProduk
                      }
                      listJenisProduk(kodeklinik:"${kodeklinik}"){
                        kodeJenisProduk
                        jenisProduk
                      }
                }
            `
			GQL(query, clientID, token).then(resp => {
				if (resp.errors) return (reject(resp.errors));;
                const kategori = resp.data.listKategoriProduk 
                const jenis = resp.data.listJenisProduk 
				if (resp.data && resp.data.listKategoriProduk) return (resolve({kategori, jenis}));
				return (resolve([]))
			}).catch(err => {
				return (reject(err));
			})
		})
    },
	listProduk: (clientID, token, kodeklinik) => {
		return new Promise((resolve, reject) => {
			const query = `
                query {
                    listProduk(kodeklinik:"${kodeklinik}"){
                        kodeklinik,
                        kodeProduk,
                        namaProduk,
                        satuan
                        harga,
                        kategori{
                            kodeKategoriProduk,
                            kategoriProduk
                        },
                        jenis{
                            kodeJenisProduk,
                            jenisProduk,
                        }
                      }
                }
            `
			GQL(query, clientID, token).then(resp => {
                const listDatas = []
                resp.data.listProduk.map(el => {
                    // console.log(el)
                    if (el.kategori) {
                        var kategori = el.kategori.kategoriProduk
                        var kodekategori = el.kategori.kodeKategoriProduk
                    } 

                    if (el.jenis) {
                        var jenis = el.jenis.jenisProduk
                        var kodejenis = el.jenis.kodeJenisProduk
                    } 

                  return listDatas.push({
                        ...el,
                        kodekategori,
                        kategori,
                        kodejenis,
                        jenis,
                    })
                })
				if (resp.errors) return (reject(resp.errors));;
				if (resp.data && resp.data.listProduk) return (resolve(listDatas));
				return (resolve([]))
			}).catch(err => {
				return (reject(err));
			})
		})
    },
    updateStokMasuk: (clientID, token,  kodeklinik, notrans, namaProduk, satuan, kode, qty) => {
        return new Promise((resolve, reject) => {
            const query =`
            mutation{
                simpanStokMasuk(
                    kodeklinik: "${kodeklinik}",
                    notrans: "${notrans}",
                    input:{
                        kodeProduk:"${kode}", 
                        namaProduk: "${namaProduk}", 
                        qty: ${qty}, 
                        satuan:"${satuan}"
                    }
                )
              }
        `
            GQL(query, clientID, token).then(resp => {
                if (resp.errors) return (reject(resp.errors));;
                if (resp.data && resp.data.simpanKlinik) return (resolve(resp.data.simpanKlinik));
                return (resolve([]))
            }).catch(err => {
                return (reject(err));
            })
        })
    },
};

export default Produk;
