import React, { useState, useEffect } from 'react'
import { Row, Col, Modal, ModalHeader, ModalBody, ModalFooter, Button } from "reactstrap";
import { TextField } from '@material-ui/core';
import mProduk from '../../../../Controler/c_StokMasuk'
import Notifikasi from '../../../../helpers/Notifikasi'
import { isArray } from "util";
import IntlMessages from "../../../../helpers/IntlMessages"
import Autocomplete from '@material-ui/lab/Autocomplete';

const ModalAdmin = (props) => {
    const clientID = props.clientID
    const token = props.token
    const detail = props.detailProduk || {}
    // const [listKategori, setlistKategori] = useState([])
    const [listProduk, setlistProduk] = useState([])
    // console.log(detail, 'detail')
    const [produk, setProduk] = useState({})
    const [qty, setQty] = useState(1)
    const klinik = props.klinik
 
    useEffect(() => {
        if (detail) {

            // console.log(detail, 'detail');
            setProduk({ 
                namaProduk: detail.namaProduk,
            })

            setQty(detail.qty)
        }
        mProduk.cekStok(clientID, token, klinik.kodeklinik).then(resp => {
            setlistProduk(resp);
        }).catch(err => {
            console.error(err);
        })
        return () => {

        }
    }, [clientID, token, klinik.kodeklinik, detail])
    const closeFrom = () => {
        props.setOpen(false)
    }


    const simpanForm = () => {
        if(!produk.namaProduk) return  Notifikasi("warning", "Wajib isi pilih produk !!")

        if (detail.notrans) {
            mProduk.updateStokMasuk(clientID, token, klinik.kodeklinik, detail.notrans, detail.namaProduk, detail.satuan, detail.kodeProduk, qty).then(resp => {
                if (resp) {
                    Notifikasi("success", "Berhasil diubah", "")
                    props.setIsReload(true)
                    closeFrom()
                } else {
                    Notifikasi("error", "Server tidak dapat menjalankan perintah Anda saat ini. Silakan coba sesaat lagi", "Gagal Ubah")
                }
            }).catch(err => {
                if (isArray(err) && err.length > 0) {
                    Notifikasi("error", err[0].message, "Gagal Ubah")
                } else if (typeof err) {
                    Notifikasi("error", err.message, "Gagal Ubah")
                } else {
                    console.log(err);
                }
            })
        }else{
            mProduk.simpanStokMasuk(clientID, token, klinik.kodeklinik, produk.namaProduk, produk.satuan, produk.kodeProduk, qty).then(resp => {
                if (resp) {
                    Notifikasi("success", "Berhasil disimpan", "")
                    props.setIsReload(true)
                    closeFrom()
                } else {
                    Notifikasi("error", "Server tidak dapat menjalankan perintah Anda saat ini. Silakan coba sesaat lagi", "Gagal Simpan")
                }
            }).catch(err => {
                if (isArray(err) && err.length > 0) {
                    Notifikasi("error", err[0].message, "Gagal Simpan")
                } else if (typeof err) {
                    Notifikasi("error", err.message, "Gagal Simpan")
                } else {
                    console.log(err);
                }
            })
        }
    
    
    
    }


    return (
        <Modal
            isOpen={props.open}
            toggle={closeFrom}
            wrapClassName="modal-right"
        >
            <ModalHeader toggle={closeFrom}>
                {detail.kodeProduk ? ("Update") : ("Add")} <IntlMessages id="menu.stok-masuk" />
                    </ModalHeader>
            <ModalBody>
                <Row>
                    <Col xs={12} md={12} className="marginFrom">
                        <Autocomplete
                            style={{ marginTop: 15 }}
                            id="combo-box-demo"
                            options={listProduk}
                            getOptionLabel={(option) => option.namaProduk}
                            fullWidth
                            value={produk}
                            onChange={(event, values) => setProduk(values)}
                            renderInput={(params) => <TextField {...params} label="Pilih produk" variant="outlined" />}
                        />
                    </Col>
                </Row>
                <Row>
                    <Col xs={12} md={12} className="marginFrom">
                        <TextField
                            id="qty"
                            name="qty"
                            label="Qty"
                            fullWidth
                            value={qty}
                            onChange={(ev) => setQty(ev.target.value)}
                            variant="outlined"
                            type="number"
                        />
                    </Col>
                </Row>
                {produk.harga && (
                    <Row>
                        <Col xs={12} md={12} className="marginFrom">
                        {/* <h4>Harga : {numberWithCommas(produk.harga)}</h4> */}
                        <h4>Satuan : {produk.satuan}</h4>
                        {/* <h4>Total : {numberWithCommas(produk.harga * qty)}</h4> */}
                        </Col>
                    </Row>
                )}
            </ModalBody>
            <ModalFooter>
                <Button color="secondary" onClick={closeFrom}>Cancel</Button>
                <Button color="primary" onClick={simpanForm}>Save <IntlMessages id="menu.stok-masuk" /></Button>{" "}
            </ModalFooter>
        </Modal>
    )
}

export default ModalAdmin;